<script>
  import { afterUpdate, onMount, tick } from "svelte";

  export let content = [];
  let article;
  let nav;


  const sections = {};
  const offset = 0;
  const scroll = (e) => {
    const scrollPosition = document.documentElement.scrollTop || document.body.scrollTop;
    for(let name in sections){
      if((sections[name] <= scrollPosition) || sections[name] < 100){
        nav?.querySelectorAll('aside .inview')?.forEach(_ => _.classList.remove('inview'));
        nav?.querySelector(`aside [data-name="${name}"]`)?.classList.add('inview');
      }
    }
  }

  const update = _ => {
    if(!article) return;
    const anchors = Array.from(article.querySelectorAll('a[name][href]'));
    anchors?.forEach((e) => (sections[e.name] = e.offsetTop - (window.innerHeight/2) + offset));
    content = anchors.map((node) => ({ label: node.textContent, href: node.getAttribute('href'), name: node.name }));
  }
  window.addEventListener('resize', update);

  afterUpdate(() => {
    update();
  });
  
  onMount(async () => {
    window.addEventListener('scroll', scroll);
    await tick();
    scroll();

    return () => {
      window.removeEventListener('resize', _ => update(ancho));
      window.removeEventListener('scroll', scroll);
    }
  });
</script>

<article bind:this={article}><slot {content} /></article>