import { writable, get } from 'svelte/store';

const themes = writable([]);

// flatten object
const objectFlat = (obj, { separator, prefix } = { prefix: '' }) => {
  const flattenRecursive = (obj, parentProperty, propertyMap = {}) => {
    for(const [key, value] of Object.entries(obj)){
      const property = parentProperty ? `${parentProperty}${separator}${key}` : `${prefix}${key}`;
      if(value && typeof value === 'object'){
        flattenRecursive(value, property, propertyMap);
      }else{
        propertyMap[property] = value;
      }
    }
    return propertyMap;
  };
  return flattenRecursive(obj);
}

const apply = cssString => document.documentElement.style.cssText = cssString;

const register = (name, theme, enable) => {
  const cssVariables = objectFlat(theme, {
    prefix: '--',
    separator: '-',
  });
  const cssString = Object.entries(cssVariables).map((([k, v]) => `${k}: ${v};`)).join('\n');
  themes.update((store) => {
    store = [...store, { name, cssString, theme }];
    if(enable){
      apply(cssString);
    }
    return store;
  });
}

const change = (name) => {
  const themesList = get(themes);
  const match = themesList.find((theme) => theme.name == name); 
  if(match){
    apply(match.cssString);
  }
}

export { register, change };
export default themes;