<script context="module">
  import { writable } from "svelte/store";
  const instances = writable(new Map());

  const setBodyScroll = (state, scroll, store) => {
    if(state && !scroll){
      document.body.classList.add('no-scroll');
    }else{
      if(!Array.from(store.values()).some(({ state }) => state)){
        document.body.classList.remove('no-scroll');
      }
    }
    return true;
  }
  
  export const clearScroll = () => setBodyScroll(true, true);
  
  const add = (id, state, scroll) => {
    instances.update((store) => {
      store.set(id, { state, scroll });
      setBodyScroll(state, scroll, store);  
      return store;
    });
  };

  const _toggle = (id, state) => {
    if(!id) {
      console.error('[svelte-ui-utils/Drawer:toggle]: id not defined');
      return;
    }
    instances.update((store) => {
      const data = store.get(id);
      if(data){
        const value = typeof state == 'boolean' ? state : !data.state;
        store.set(id, { ...data, state: value });
        setBodyScroll(value, data.scroll, store);  
      }
      return store;
    });
  };
  export const toggle = (id) => _toggle(id); 
  export const trigger = (id, state) => _toggle(id, state);
</script>

<script>
  export let id;
  export let overlay = undefined;
  export let open = false;
  export let left = undefined;
  export let right = undefined;
  export let top = undefined;
  export let bottom = undefined;
  export let width = undefined;
  export let height = undefined;
  export let duration = 400;
  export let scroll = false;
  export let zindex = 1;

  if(Object.values({ left, right, top, bottom}).filter(value => value == true).length !== 1){
    throw new Error('[svelte-ui-utils/Drawer]: only one of top, left, bottom, right needs to be present');
  }
  if(!id) {
    throw new Error('[svelte-ui-utils/Drawer]: id not defined');
  }
  add(id, !!open, !!scroll);
</script>

{#if overlay && $instances.get(id).state}
  <slot name="overlay" open={$instances.get(id).state}><div class="overlay" class:active={$instances.get(id).state} style="--zindex: {zindex};" on:click={_ => toggle(id, false)}></div></slot>
{/if}
<div 
  class:drawer={true} 
  class:active={$instances.get(id).state} 
  class:width={width} 
  class:height={height} 
  style="--width: {width}; --height: {height}; --duration: {duration}ms; --zindex: {zindex};" 
  class:top 
  class:right 
  class:bottom
  class:left 
  {...$$restProps}
  on:click
  on:mouseenter
  on:mouseleave
  >
  <slot open={$instances.get(id).state} />
</div>

<style>
  .overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .2);
    z-index: var(--zindex);
  }
  .drawer{
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: var(--zindex);
    transition: transform var(--duration, .3s);
  }
  .top{
    top: 0;
    left: 0;
    transform: translateY(-100%);
    height: auto;
  }
  .bottom{
    left: 0;
    bottom: 0;
    transform: translateY(100%);
    height: auto;
  }
  .left{
    top: 0;
    left: 0;
    transform: translateX(-100%);
    width: auto;
  }
  .right{
    top: 0;
    right: 0;
    transform: translateX(100%);
    width: auto;
  }
  .height{
    height: var(--height);
  }
  .width{
    width: var(--width);
  }
  .active{
    transform: translateX(0);
  }
</style>