<script context="module">
  import { writable, get } from "svelte/store";
  const defaults = [];
  const breakpoints = writable(defaults);
  export const setBreakpoints = (map) => breakpoints.update((store) => {
    let output = [];
    Object.entries(map).forEach(([k, v]) => {
      if(Number.isInteger(v) && !(k in store)){
        const exist = Object.values(store).some(value => value == v);
        if(!exist){
          output.push([k, v]);
        }else{
          console.error(`Breakpoint value '${v}' already exist. { ${k}: ${v} }`);
        }
      }
    });
    output.sort((a, b) => a[1] - b[1]);
    return output;
  });
</script>
<script>
  export let width = undefined, match = false, equal = false, below = false, above = false, at;
  
  const findClosest = (needle, above, below) => {
    const closestBreakpoint = $breakpoints.reduce((a, b) => {
      const aDiff = Math.abs(a[1] - needle);
      const bDiff = Math.abs(b[1] - needle);
      return bDiff < aDiff ? b : a;
    });
    // TODO: we need to find the closest index. then check to see if the current width of the window is below or above
    // and also check if equal is set, then we need to account for that. then we can find the current breakpoint according to the current
    // width. because we want to find different values depending on if below or above is defined.
    const indexOf = $breakpoints.findIndex(([k, v]) => k == closestBreakpoint[0]);
    if(indexOf > -1){
      if(below){
        let current = $breakpoints[indexOf];
        if(current){
          let index = needle < match[1] ? indexOf-1 : indexOf;
          current = $breakpoints[index];
          // console.log(current)
        }
      }
    }
  };
  
  if(!Object.keys(get(breakpoints)).length){
    console.error(`[svelte-ui-utils:breakpoint]: no breakpoints defined.`);
  }
  $: findClosest(width, above, below, equal);
  $: [ breakpointKey, breakpointValue ] = $breakpoints.find(([k, v]) => k == at);
</script>

<!-- While debugging in browser mobile view the correct innerWidth is not displayed from the beginnning. will it be an issue even when being live? -->
<svelte:window bind:innerWidth={width} />

{#if (match || (equal && width == breakpointValue) || (below && width < breakpointValue) || (above && width > breakpointValue))}
  <slot equal={(equal && width == breakpointValue)} match={((equal && width == breakpointValue) || (below && width < breakpointValue) || (above && width > breakpointValue))} {width}>{width}</slot>
{/if}