import RouterComponent, {
  context,
  Router,
  decorator,
} from "svelte-standalone-router";

// _layouts
import _error from "./_layout/_error.svelte";
import _documentation from "./_layout/_documentation.svelte";

// pages
import Index from "./pages/Index.svelte";
import Documentation from "./pages/Documentation.svx";
import Changelog from "./pages/Changelog.svx";
import Guides from "./pages/Guides.svx";
import Error from "./pages/Error.svelte";

Router.scrollOffset = 100;

// let ENV = typeof process !== "undefined" && process.env && process.env.NODE_ENV;
// let DEV = ENV !== "production";

// if (!DEV) {
//   Router.linkBase = "/svelte-standalone-router";
// }

// implementaiton
Router.linkBase = "/svelte-standalone-router";
const app = context({
  initial: location.pathname,
  base: Router.linkBase,
});

// catch fallbacks
app.catch((req, res, props) => res.send(Error, { time: 5 }));

// decorators
const documentation = decorator(_documentation);

// routes
// main
documentation("/", (req, res) => res.send(Index));

// documentation
documentation("/how-to/documentation", async (req, res) =>
  res.send(Documentation)
);
documentation("/how-to/guides", (req, res) => res.send(Guides));
documentation("/changelog", (req, res) => res.send(Changelog));
